.loginPage {
  background: url('../beans.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 800px;
}

.centerCol {
  margin: 0 auto;
  float: none;
  min-width: 300px;
}

.center {
  margin: 0 auto;
  float: none;
}

.headings {
  background: #dd8047;
}

.lowerTop {
  margin-top: 50px;
}

.gridLoader {
  display: inline-block;
  float: none;
  margin: 10px 0 10px 0;
}

.productCol {
  position: relative;
  margin: 0 auto;
  float: none;
  margin-top: 5%;
}

.rotatingBean {
  -webkit-animation: rotatingBean 1s infinite linear;
          animation: rotatingBean 1s infinite linear;
}

@-webkit-keyframes rotatingBean {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(720deg);
                transform: rotate(720deg);
    }
}

@keyframes rotatingBean {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(720deg);
                transform: rotate(720deg);
    }
}

.rotatingCup {
  -webkit-animation: rotatingCup 1s infinite linear;
          animation: rotatingCup 1s infinite linear;
}

@-webkit-keyframes rotatingCup {
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

@keyframes rotatingCup {
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

.product {
  position: relative;
  display: inline-block;
  width: 256px;
  margin: 0 auto;
  -webkit-box-shadow: 2px 2px 4px rgba(0,0,0,0.15);
          box-shadow: 2px 2px 4px rgba(0,0,0,0.15);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.product-no-hover {
  position: relative;
  display: inline-block;
  width: 256px;
  margin: 0 auto;
  float: none;
  -webkit-box-shadow: 2px 2px 4px rgba(0,0,0,0.15);
          box-shadow: 2px 2px 4px rgba(0,0,0,0.15);
  margin-bottom: 10px;
}

.product::after {
  position: absolute;
  z-index: -1;
  opacity: 0;
  -webkit-box-shadow: 20px 20px 15px rgba(0,0,0,0.3);
          box-shadow: 20px 20px 15px rgba(0,0,0,0.3);
  -webkit-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}

.product:hover {
  -webkit-transform: scale(1.05, 1.05);
      -ms-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.product:hover::after {
  opacity: 1;
}

.product > a {
  text-decoration: none;
}

#jumbo {
  text-align: center;
  background-color: #74533f;
  width: 100%;
  border-radius: 0px;
  margin: 0px;
  border-top: 2px solid #dd8047;
  border-bottom: 2px solid #dd8047;
}

.profilePhoto {
  width: 200px;
  height: auto;
  border: 2px solid #dd8047;
}

.navbar {
  margin: 0px;
}

.navbar-nav > li > a > a {
  font-size: 22px;
  font-weight: 100;
  text-decoration: none;
  color: grey;
}

.navbar-nav > li > a > a:hover {
 color: black;
}

.page-footer {
  clear: both;
  width: 100%;
  height: 150px;
  margin: 0;
  background-color: #74533f;
}

.wrap{
  margin: 0;
  min-height: calc(100vh - 150px);
}

.footer-copyright {
  top: 50px;
  text-align: center;
}

.topAndBottom {
  margin-top: 40px;
  margin-bottom: 40px;
}

.addButton {
  float: right;
  margin: 5px;
  position: absolute;
  top: 0px;
  right: 0px;
}

.backButton {
  float: right;
  margin: 15px;
  position: absolute;
  top: 0px;
  right: 0px;
}

.dropdown-menu > li > a > a {
  text-decoration: none;
  color: #333;
}

.landing {
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 5.55);
  -o-transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 5.55);
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 5.55);
}

.animated-landing {
  opacity: 0;
  -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
          transform: translateY(30px);
}

.mapboxgl-marker > img {
 background-image: url('../coffee-cup.png');
 background-size: cover;
 width: 20px;
 height: 20px;
 border-radius: 50%;
 cursor: pointer;
}

.mapboxgl-map{
  margin:0;
  padding: 20px;
}

@media screen and (min-width: 769px) {
  .mapboxgl-map{
    position: fixed;
    margin:0;
    padding: 20px;
  }

}


